.m-card {
  background-color: $color-black;
  color: #fff;
  padding: 20px 30px 30px;
  border: 1px solid darken($color-hot-yellow-100, 30%);
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  max-width: 340px;
  margin: 0 auto;

  &.m-card--collapse {
    background: none;
    color: var(--global--color-black);
    border: none;
    border-top: 1px solid var(--global--color-black);
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 20px;
    flex-direction: row-reverse;
  }

  &.m-card--title-only {
    align-items: center;
  }

  a {
    color: inherit;
  }
}

.m-card__text {
  .m-card--collapse & {
    width: calc(100% - (75px + 15px));
  }
}

.m-card__meta {
  display: flex;
  min-height: 29px;
}

.m-card__content-category,
.o-post-header__content-category {
  text-transform: uppercase;
  margin-right: 18px;

  a {
    color: #fff;
    text-decoration: none;
    font-size: rem(16px);

    &:hover {
      text-decoration: underline;
    }
  }
}

.m-card__category,
.o-post-header__primary-category {
  font-family: $mono;
  text-transform: uppercase;
  line-height: .9;

  a {
    color: $color-hot-yellow-100;
    font-size: rem(14px);
    text-decoration: none;

    .m-card--collapse & {
      color: var(--global--color-black);
    }

    &:hover {
      text-decoration: underline;
    }
  }
}

.m-card__is-hot,
.o-post-header__is-hot {
  margin-left: 8px;

  i {
    color: $color-hot-yellow-100;
    font-size: rem(12px);

    .m-card--collapse & {
      color: var(--global--color-black);
    }
  }
}

.m-card__title {
  font-family: $judgetrial;
  font-weight: 500;
  color: #fff;
  font-size: rem(24px);
  line-height: 1.1666;
  text-transform: uppercase;
  margin-bottom: 30px;

  .m-card--collapse & {
    margin-bottom: 0;
    font-size: rem(14px);
    font-family: $monospace;
    line-height: 1.5;

    a {
      color: var(--global--color-black);
    }
  }

  a {
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }
}

.m-card__desc {
  color: var(--global--color-black);
  margin: 0;
  font-size: rem(12px) !important;
  font-family: $monospace;
  line-height: 1.5;
}

.m-card__image-link {
  display: block;
  width: 100%;
  height: auto;
  clip-path: circle(50%);
  position: relative;

  &:hover + .m-card__text .m-card__title a {
    text-decoration: underline;
  }

  .m-card--collapse & {
    max-width: 75px;
  }

  img {
    width: 100%;
    height: auto;
    object-fit: cover;
  }

  &.watch {
    &:before {
      content: "";
      position: absolute;
      height: 100px;
      width: 100px;
      top: calc(50% - 50px);
      left: calc(50% - 50px);
      z-index: 1000;
      background-image: url(../images/playbutton.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      pointer-events: none;
    }
  }
}
